import React, { Fragment, useState } from 'react';
import { useDispatch, useSelector } from "react-redux";
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { getLoginAccion } from "../../redux/LoginDucks";
import { useForm } from 'react-hook-form'
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <Link color="inherit" href="https://material-ui.com/">
                Centinela Software
      </Link>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

export default function SignIn() {
    //Store y configuracion REDUX
    const dispatch = useDispatch();
    const loginRedux = useSelector((store) => store.login.array);
    const reload = useSelector((store) => store.login.reload);
    const msg = useSelector((store) => store.login.message);
    const error = useSelector((store) => store.login.error);

    //=================
    const [message, setMessage] = useState("Hola tarolas como estas");
    const [severity, setSeverity] = useState("")
    const [open, setOpen] = React.useState(false);

    const handleClick = () => {
        setOpen(true);
    };


    const handleMessage = () => {
        if (error) {
            let json = JSON.parse(msg);
            setMessage(json.CustomMessage)
            setSeverity("error")
            handleClick();
        }
    }

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        setOpen(false);
    };
    //=================





    //Estilos
    const classes = useStyles();

    //react-hook-forms
    const { register, errors, handleSubmit } = useForm();

    //HandleClick
    const onSubmit = (data) => {
        dispatch(getLoginAccion(data));
    }

    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Identificate
        </Typography>
                <form className={classes.form} onSubmit={handleSubmit(onSubmit)} noValidate autoComplete="off">
                    <h6><span className="text-danger text-small d-block mb-2">{errors?.usuario?.message}</span></h6>
                    <TextField inputRef={register({ required: { value: true, message: 'Usuario requerido' } })}
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        id="usuario"
                        label="Usuario"
                        name="usuario"
                        autoComplete="usuario"
                        autoFocus
                    />
                    <h6><span className="text-danger text-small d-block mb-2">{errors?.password?.message}</span></h6>
                    <TextField inputRef={register({ required: { value: true, message: 'Password requerido' } })}
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        name="password"
                        label="Password"
                        // type="password"
                        id="password"
                        autoComplete="current-password"
                    />
                    {/* <FormControlLabel
                        control={<Checkbox value="remember" color="primary" />}
                        label="Remember me"
                    /> */}
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                        onClick={handleMessage}
                    >
                        Acceder
          </Button>
                    <div className={classes.root}>
                        <Snackbar open={open} autoHideDuration={3000} onClose={handleClose}>
                            <Alert onClose={handleClose} severity={severity}>
                                {message}
                            </Alert>
                        </Snackbar>
                    </div>
                    {/* <Grid container>
                        <Grid item xs>
                            <Link href="#" variant="body2">
                                Forgot password?
              </Link>
                        </Grid>
                        <Grid item>
                            <Link href="#" variant="body2">
                                {"Don't have an account? Sign Up"}
                            </Link>
                        </Grid>
                    </Grid> */}
                </form>
            </div>
            <Box mt={8}>
                <Copyright />
            </Box>
        </Container>
    );
}